import { IndexAPI } from ".";
import { config } from "../config";
import { Request } from "../enums/purpose"

const $requests = IndexAPI(config.REQUESTS, "notauth");

class RequestsService{
    async getBanks(request:Request){
        // new URLSearchParams(JSON.stringify(request))
        return await $requests.get(`/key/data-for-table?amount=${request.amount}&term=${request.term}&purpose=${request.purpose}`)
    }
    async getDepositBanks(request:string){
        // new URLSearchParams(JSON.stringify(request))
        return await $requests.get(`https://public.sravni.ru/v1/deposits/products?${request}`);
    }
    async getConverterBanks(){
        return await $requests.get(`https://www.cbr-xml-daily.ru/daily_json.js`);
    }
}

export default new RequestsService()